import React from 'react';
import {connect} from 'react-redux';
import vkbridge from '@vkontakte/vk-bridge';
import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";
import {restoreScrollPosition} from "../../services/_functions";
import Icon28Message from '@vkontakte/icons/dist/28/message';
import Icon28More from '@vkontakte/icons/dist/28/more';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';
import Icon28MessagesOutline from '@vkontakte/icons/dist/28/messages_outline';
import Icon28UserAddOutline from '@vkontakte/icons/dist/28/user_add_outline';
import Icon24Game from '@vkontakte/icons/dist/24/game';
import Icon56ErrorOutline from '@vkontakte/icons/dist/56/error_outline';
import Icon56FireOutline from '@vkontakte/icons/dist/56/fire_outline';
import Icon56CheckCircleOutline from '@vkontakte/icons/dist/56/check_circle_outline';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon16CancelCircleOutline from '@vkontakte/icons/dist/16/cancel_circle_outline';
import {
    Div,
    Panel,
    Group,
    CellButton,
    PanelHeader,
    Alert,
    Switch,
    Footer,
    FixedLayout,
    Button,
    HeaderButton,
    Avatar,
    Checkbox,
    Link,
    Cell,
    List,
    FormLayout,
    FormLayoutGroup,
    Input,
    PromoBanner,
    PanelSpinner,
    ScreenSpinner,
    PanelHeaderBack,
    Placeholder,
    Snackbar,
    Select,
    Spinner,
    Separator,
    HorizontalScroll,
    TabsItem,
    Tabs
} from "@vkontakte/vkui";
const blueBackground = {backgroundColor: 'var(--accent)'};
const qs = require('querystring');
var params = window.location.search.replace('?', '').replace('%2C', ',');
const urlParams = qs.parse(params);
const ordered = {};
Object.keys(urlParams).sort().forEach((key) => {
if (key.slice(0, 3) === 'vk_') {
ordered[key] = urlParams[key];
}
});
var button;
class HomePanelProfile extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            activeTab: props.activeTab["EXAMPLE"] || "modal",
            result: 0,
            fetchedUser: '',
            popout: null,
            viewLoaded: 'false',
            value1: '',
            formula: '',
            formula2: '',
            valid: 'default',
            snackbar: null,
            button: '',
            wiki: '',
            massa: '',
            input2: '',
            load: false,
            time: '',
            fel: '',
            promoBannerProps: ''
        };
    }

onChanges = (e) => {
const { name, value } = e.currentTarget;
this.setState({ [name]: value, button: 'true'});

}

 onClick () {
    this.setState({popout: <ScreenSpinner />});
    setTimeout(() => { this.setState({ popout: null }) }, 2000);
  }

    setTab(tab) {
        this.setState({
            activeTab: tab
        });
    }

    openPopoutInternet() {
            this.props.openPopout(
                <Alert
                    actions={[{
                        title: 'Ясно',
                        autoclose: true,
                        style: 'cancel',
                    }]}
                    onClose={() => this.props.closePopout()}
                >
                    <h2>Информация</h2>
                    <p>Не удалось получить данные, возможно, пропало подключение к интернету.</p>
                </Alert>
            );
        }

	    componentWillUnmount() {
        const {setScrollPositionByID, setActiveTab} = this.props;
        setActiveTab("EXAMPLE", this.state.activeTab);
        setScrollPositionByID("EXAMPLE_TABS_LIST");
    }



    componentDidMount() {
      vkbridge.send('VKWebAppGetAds') .then((promoBannerData) => { 
        this.setState({ promoBannerProps: promoBannerData });
        console.log(promoBannerData);
    }); 
        restoreScrollPosition();
vkbridge.send("VKWebAppGetUserInfo", {});
vkbridge.subscribe((e) => {
console.log('subscribe');
switch(e.detail.type) { case 'VKWebAppGetUserInfoResult':
console.log(e.detail.data);
this.setState({fetchedUser: e.detail.data});
break;
 }
 });
}
copyWiki = () => {
    if(this.state.wiki === 'Нет информации.') return this.setState({ snackbar:<Snackbar layout="vertical" onClose={() => this.setState({ snackbar: null })} before={<Avatar size={24} style={blueBackground}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}> Не удалось скопировать информацию, т.к. ее нет.</Snackbar>});
    navigator.clipboard.writeText(this.state.wiki)
    .then(text => {
        this.setState({ snackbar:
            <Snackbar
            layout="vertical"
            onClose={() => this.setState({ snackbar: null })}
            before={<Avatar size={24} style={blueBackground}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}>
            Вы успешно скопировали информацию с Wikipedia.
            </Snackbar>
            })
      })
      .catch(err => {
        this.setState({ snackbar:
            <Snackbar
            layout="vertical"
            onClose={() => this.setState({ snackbar: null })}
            before={<Avatar size={24} style={blueBackground}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}>
            Не удалось скопировать информацию с Wikipedia.
            </Snackbar>
            })
      });;}

check = () => {
    console.log(this.state.input2);
this.setState({button: '', viewLoaded: 'true', snackbar: null});
fetch(`https://cors-anywhere.herokuapp.com/https://peaceful-oasis-84546.herokuapp.com/api.php?${window.location.search.replace("?", "")}&method=solve.formula&formula=${this.state.value1}`)
.then(res => res.json())
.then(res => {
this.setState({massa: res.massa, formula: res.formula, wiki: res.wiki, element: this.state.value1, viewLoaded: 'false', time: res.time, load: res.response, fel: res.fel});
if(res.response === false){
if (this.state.snackbar) return;
this.setState({ snackbar:
<Snackbar
layout="vertical"
onClose={() => this.setState({ snackbar: null })}
before={<Avatar size={24} style={blueBackground}><Icon16CancelCircleOutline fill="#fff" width={14} height={14} /></Avatar>}>
Нам не удалось получить данные. Обратите внимание, что регистр важен при поиске.
</Snackbar>
});
}
}).catch(e => {
if (this.state.snackbar){
  this.setState({ snackbar:
<Snackbar
layout="vertical"
onClose={() => this.setState({ snackbar: null })}
before={<Avatar size={24} style={blueBackground}><Icon16CancelCircleOutline fill="#fff" width={14} height={14} /></Avatar>}>
Нам не удалось получить данные. Возможно, пропал интернет.
</Snackbar>, viewLoaded: 'false'    
})  
};
this.setState({ snackbar:
<Snackbar
layout="vertical"
onClose={() => this.setState({ snackbar: null })}
before={<Avatar size={24} style={blueBackground}><Icon16CancelCircleOutline fill="#fff" width={14} height={14} /></Avatar>}>
Нам не удалось получить данные. Возможно, пропал интернет.
</Snackbar>, viewLoaded: 'false'    
})
})
}
copyDev = () => { 
vkbridge.send("VKWebAppCopyText", {text: this.state.time});
if (this.state.snackbar) return;
this.setState({ snackbar:
<Snackbar
layout="vertical"
onClose={() => this.setState({ snackbar: null })}
before={<Avatar size={24} style={blueBackground}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}>
Вы успешно скопировали Debug информацию.
</Snackbar>
});
              setTimeout(() => {
            this.setState({ snackbar: null });
         }, 9000);
}

    render(){
if(this.state.value1 === ''){
button = <Button disabled size="xl" level="secondary" onClick={(this.check)}>Узнать</Button>;
} else if(this.state.value1 != ''){
button = <Button size="xl" level="secondary" onClick={(this.check)}>Узнать</Button>;
}
if(this.state.viewLoaded === 'true'){
   button = <Spinner size="large" style={{ marginTop: 20, color: '#4680C2' }}></Spinner>;
}
        const {id, setPage, withoutEpic} = this.props;
        const boxStyle = {marginTop: 56};
        const { viewLoaded } = this.state;
        return (

            <Panel id={id}>
            {this.state.load === false ?
            <PanelHeader>Вещества</PanelHeader>:
                <PanelHeader left={<PanelHeaderBack onClick={() => this.setState({load: false, value1: ''})}/>}>Вещества</PanelHeader>}
                {this.state.load === false ? 
                <Placeholder header="Определитель элементов по их формулам." icon={<Icon56FireOutline fill='#4680C2'/>}>
Обратите внимание, что поиск доступен только по формуле.<br/> <br/>
<FormLayoutGroup>
<Input
value={this.state.value1}
type="text"
name="value1"
placeholder="H2SO4"
maxLength="39"
onChange={this.onChanges}
status={this.state.value1 ? 'valid' : this.state.valid} onKeyDown={this.func}
/>
<br/>
{button}
</FormLayoutGroup>
{ordered.vk_platform != 'mobile_android' && ordered.vk_platform != 'desktop_web' && ordered.vk_platform != 'mobile_android_messenger' &&
          <FixedLayout vertical="bottom">
  <PromoBanner bannerData={this.state.promoBannerProps} isCloseButtonHidden/>
        </FixedLayout>}
</Placeholder>
:
<Placeholder icon={<Icon56CheckCircleOutline onClick={(this.copyDev)} fill='#4680C2' width={50} height={50}/>}> 
Информация о {this.state.fel} была успешно получена. <br/><br/>
Формула: {this.state.fel}<br/>
Молярная масса: {this.state.massa}<br/> 
Источник: <Link href="https://pubchem.ncbi.nlm.nih.gov/" target="_blank">PubChem</Link><br/><br/>
Информация с <Link href="https://ru.wikipedia.org" target="_blank">Wikipedia</Link>: {this.state.wiki} <br/>
<Div>
<Button stretched mode="secondary" size="l" onClick={(this.copyWiki)}>Скопировать</Button>
</Div>
{ordered.vk_platform != 'mobile_android' && ordered.vk_platform != 'desktop_web' && ordered.vk_platform != 'mobile_android_messenger' &&
          <FixedLayout vertical="bottom">
  <PromoBanner bannerData={this.state.promoBannerProps} isCloseButtonHidden/>
        </FixedLayout>}
</Placeholder>}
{this.state.snackbar}
  </Panel>
        );
    }

}

const mapStateToProps = (state) => {
    return {
        activeTab: state.vkui.activeTab,
    };
};

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal,
    setActiveTab,
    setScrollPositionByID
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePanelProfile);